import { validateForms } from '../functions/validate-forms';
const rules1 = [
  {
    ruleSelector: '.subscribe__input',
    rules: [
      {
        rule: 'required',
        value: true,
        errorMessage: 'Заполните Email',
      },
      {
        rule: 'email',
        errorMessage: 'Неверный формат Email',
      },

    ]
  },
];

const rules2 = [
  {
    ruleSelector: '.feedback__input--name',
    rules: [
      {
        rule: 'required',
        value: true,
        errorMessage: 'Заполните Имя',
      },
      {
        rule: 'minLength',
        value: 3,
        errorMessage: 'Имя должно состоять хотя бы из 3-х символов',
      },
    ]
  },
  {
    ruleSelector: '.feedback__input--email',
    rules: [
      {
        rule: 'required',
        value: true,
        errorMessage: 'Заполните Email',
      },
      {
        rule: 'email',
        errorMessage: 'Неверный формат Email',
      },
    ]
  },
];

const subscribeForm = document.querySelector('.subscribe__form');
subscribeForm.addEventListener('submit', (e) => {
  e.preventDefault();
});

const feedbackForm = document.querySelector('.feedback__form');
feedbackForm.addEventListener('submit', (e) => {
  e.preventDefault();
});

validateForms('.subscribe__form', rules1);
validateForms('.feedback__form', rules2);

